

const getLogoUrl = (url, backendUrl) => {


  const httpBackendUrl = backendUrl.replace('ws://', 'http://').replace('wss://', 'https://');
  const components = httpBackendUrl.split('/')

  return components[0] + '//' + components[2] + "/cache//logos//" + url;
};

const convertConfig = (config, backendUrl) => {
  config.frontend.blueTeam.logo = getLogoUrl(config.frontend.blueTeam.logo, backendUrl);
  config.frontend.redTeam.logo = getLogoUrl(config.frontend.redTeam.logo, backendUrl);
  config.frontend.tournament = getLogoUrl(config.frontend.tournament, backendUrl);
  return config;
}

export default convertConfig;
